
import { defineComponent, computed } from 'vue';

export default defineComponent({
  props: {
    outline: {
      type: Boolean,
      default: false,
    },

    disabled: {
      type: Boolean,
      default: false,
    },

    small: {
      type: Boolean,
      default: false,
    },
  },

  setup(props) {
    const rootClasses = computed(() => ({
      small: props.small,
      outline: props.outline,
      h3: true,
    }));

    return {
      rootClasses,
    };
  },
});
