
import type { LabelValue } from '@/types';
import { defineComponent } from 'vue';

export default defineComponent({
  props: {
    options: {
      type: Array as () => LabelValue<any>[],
      required: true,
    },

    modelValue: {
      required: true,
    },

    small: {
      type: Boolean,
      default: false,
    },
  },

  emits: ['update:modelValue'],

  setup(props, { emit }) {
    const setActive = (value: any) => emit('update:modelValue', value);

    return {
      setActive,
    };
  },
});
